.parallax {
  perspective: 300px;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
}
.parallax__layer {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.parallax__layer--fore {
  transform: translateZ(90px) scale(.7);
}

.parallax__layer--base {
  transform: translateZ(0);
}

.parallax__layer--closer {
  transform: translateZ(-150px) scale(1.5);
}

.parallax__layer--back {
  transform: translateZ(-300px) scale(2);
}

.parallax__layer--deep {
  transform: translateZ(-600px) scale(3);
}

.parallax__group {
  position: relative;
  height: 100vh;
  transform-style: preserve-3d;
  /* transform: translate3d(700px, 0, -800px) rotateY(30deg); */
}

.img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: none;
  width:100%;
  height:100%;
  object-fit: cover;
  overflow: hidden;
}

.clouds {
  opacity: 0.3;
}

h1{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

div {
  display: block;
}

#plane-group{
  transition: all 0.2s ease;
  transform: translate(100%, -50%);
  overflow: visible;
}

#plane{
  z-index: 5;
  width: 15vh;
  position: absolute;
  top: 50%;
}

#trail {
  background-color: lightgoldenrodyellow;
  height: 0.5vh;
  width: 200vw;
  top: 53.5vh;
  left: 3vh;
  position: relative;
  z-index: -1
}

#group1 {
  z-index: 5;
}

#group2 {
  z-index: 3;
}

#group3 {
  z-index: 1;
}

#group4 {
  z-index: 3;
}
